'use client';
import axios from "axios";
import React, { useState, useEffect } from "react";
import { useRouter } from 'next/navigation';

const fetchLecturers = async () => {
  try {
    const response = await axios.get("http://localhost:9921/lecturers");
    return response.data;
  } catch (error) {
    console.error("Error fetching lecturers:", error);
    return [];
  }
};

const LecturersPage = () => {
  const [lecturers, setLecturers] = useState([]);
  const [filteredLecturers, setFilteredLecturers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeCategory, setActiveCategory] = useState("All Lecturers");
  const router = useRouter();

  useEffect(() => {
    const getLecturers = async () => {
      const data = await fetchLecturers();
      setLecturers(data);
      setFilteredLecturers(data);
      setLoading(false);
    };
    getLecturers();
  }, []);

  const handleCategoryClick = (category) => {
    setActiveCategory(category);
    if (category === "All Lecturers") {
      setFilteredLecturers(lecturers);
    } else {
      setFilteredLecturers(lecturers.filter((lecturer) => lecturer.lecturerType === category));
    }
  };

  const handleDeleteLecturer = async (lecturerId) => {
    try {
      await axios.delete(`http://localhost:9921/lecturers/${lecturerId}`);
      const updatedLecturers = lecturers.filter((lecturer) => lecturer.lecturerId !== lecturerId);
      setLecturers(updatedLecturers);
      setFilteredLecturers(updatedLecturers);
    } catch (error) {
      console.error("Error deleting Lecturer:", error);
    }
  };

  const handleEditLecturer = (lecturerId) => {
    router.push(`/lecturers/edit/${lecturerId}`);
  };

  return (
    <div className="p-6 w-full max-w-7xl mx-auto">
      <div className="mb-6 flex flex-col sm:flex-row justify-between items-start sm:items-center">
        <div>
          <h3 className="text-xl sm:text-2xl text-green-900 font-semibold">Lecturers Management</h3>
          <p className="text-sm text-gray-500">Manage and monitor all available Lecturers</p>
        </div>
        <button
          onClick={() => router.push('/lecturers/add')}
          className="mt-4 sm:mt-0 px-4 py-2 bg-indigo-600 text-white rounded-md text-sm font-medium hover:bg-indigo-700 transition"
        >
          Add New Lecturer
        </button>
      </div>

      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <div className="p-4 border-b flex gap-2 sm:gap-4">
          {["All Lecturers"].map((category) => (
            <button
              key={category}
              className={`px-3 py-1 text-sm font-medium rounded-md transition ${
                activeCategory === category 
                  ? "bg-indigo-100 text-indigo-600" 
                  : "bg-gray-100 text-gray-600 hover:bg-gray-200"
              }`}
              onClick={() => handleCategoryClick(category)}
            >
              {category}
            </button>
          ))}
        </div>

        <div className="overflow-x-auto">
          {loading ? (
            <div className="p-4 text-center text-gray-500">Loading Lecturers...</div>
          ) : (
            <table className="w-full min-w-max divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Lecturer ID</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">First Name</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Last Name</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Sessions Assigned</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Actions</th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredLecturers.length > 0 ? (
                  filteredLecturers.map((lecturer) => (
                    <tr key={lecturer.lecturerId} className="hover:bg-gray-50 transition">
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{lecturer.lecturerId}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{lecturer.firstName}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{lecturer.lastName}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{lecturer.sessionsAssigned}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        <div className="flex gap-2">
                          <button
                            className="text-indigo-600 hover:text-indigo-900 transition"
                            onClick={() => handleEditLecturer(lecturer.lecturerId)}
                          >
                            Edit
                          </button>
                          <button
                            className="text-red-600 hover:text-red-900 transition"
                            onClick={() => handleDeleteLecturer(lecturer.lecturerId)}
                          >
                            Delete
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="5" className="px-6 py-4 text-center text-gray-500">No Lecturers found.</td>
                  </tr>
                )}
              </tbody>
            </table>
          )}
        </div>
      </div>
    </div>
  );
};

export default LecturersPage;
